html{
    width: 100%;
    height: 100vh; 
    background: linear-gradient(rgb(17, 153, 216),rgb(188, 5, 194),rgb(35, 37, 175));
    display: flex;
    justify-content: center;
}
.gameSquares{
    margin: 100px auto;
    width: 300px;
    height: 300px;
    border: 1px solid black;
    border-radius: 10px;
    background-color: rgb(253, 253, 253);
    z-index: 9999;

}
.squares{
    width: 100px;
    height: 100px;
    border: 1px solid rgb(162, 0, 255);
    float: left;
    box-sizing: border-box;
    text-align: center;
}

.dagger {
position: relative;
width: 100px;
height: 100px;

}
.dagger:before, .dagger:after {
position: absolute;
content: '';
height: 105px;
width: 10px;
background-color: rgb(159, 7, 219);
}
.dagger:before {
transform: rotate(45deg);
}
.dagger:after {
transform: rotate(-45deg);
}


.circle{
    position: relative;
}
.circle::after{
    position:absolute;
    content: "";
    width: 85px;
    height: 85px;
    border: 5px solid rgb(11, 177, 228);
    border-radius: 50px;
    left: 2px;
    top:2px;
}
.players{
    text-align: center;
}
.buttonNewGame{
    outline:none;
    inline-size: none;
    font-size: x-large;
    border: 3px solid black;
    border-radius: 20px;
    width: 200px;
    height: 30px;
    background-color: turquoise;
    font-family: monospace;
}

.winClose{
    text-align: center;
    width: 350px;
    font-size: xx-large;
    font-family: monospace;
    position: absolute;
    top: 5px;
    color: rgb(60, 255, 0);
}